/*
    webstorage

    session: expired=true
    local: expired=number|default

    set  :
        expired < 0 || expire === true  sessionStorage
        expired > 1000000000000   
        else   maxage
    get  :
        forceFormattedStorage  
*/
const defaultExpired = 20 * 24 * 3600 * 1000;

let storage = {
    set(key, value, expired) {
        let webStorage = window.localStorage;
        if (expired === true || +expired < 0) {
            // sessionStorage
            webStorage = window.sessionStorage;
        } else if (+expired && +expired > 1000000000000) {
            // 
            expired = +expired;
        } else {
            // 
            expired = Date.now() + (+expired || defaultExpired);
        }
        try {
            webStorage.setItem(
                key,
                JSON.stringify({
                    expired: expired,
                    value: value
                })
            );
        } catch (e) {
            console.error('webStorage', e);
        }
    },
    get(key, forceFormattedStorage = true) {
        let data = isStorageKey(key);
        if (!data) {
            // storage  
            return forceFormattedStorage ? null : getItem(key);
        }
        // 
        if (data.expired !== true && +data.expired <= Date.now()) {
            storage.remove(key);
            return null;
        }
        return data.value;
    },
    remove(key) {
        try{
            window.localStorage.removeItem(key);
            window.sessionStorage.removeItem(key);
        }catch(e) {
            console.error('webStorage', e);
        }
    },
    clear(force) {
        if (!checkSupport()) {
            return null;
        }
        if (force) {
            window.localStorage.clear();
            window.sessionStorage.clear();
        } else {
            Object.keys(window.localStorage).forEach(key => {
                let data = isStorageKey(key);
                if (!data) {
                    return;
                }
                if (+data.expired <= Date.now()) {
                    storage.remove(key);
                }
            });
        }
    }
};

setTimeout(_ => {
    storage.clear();
}, 3000);

export default storage;


function getItem(key) {
    return window.sessionStorage.getItem(key) || window.localStorage.getItem(key);
}

function checkSupport() {
    return window.localStorage && window.sessionStorage;
}

function isStorageKey(key) {
    if (!checkSupport()) {
        return null;
    }
    let data = getItem(key);
    // key
    if (!data) {
        return data;
    }
    try {
        data = JSON.parse(data);
        // 
        if (!data.expired && !data.hasOwnProperty('value')) {
            data = null;
        }
    } catch (e) {
        console.warn('webStorage', key, e);
    }
    return data;
}
